/**
 * AI
 *
 */
const AILogic = function (p) {
  this.player = p;
  this.cards = p.cardList.slice(0);
  this.analyse();
};
// ai
const AICardType = (val, cardList) => ({val, cardList})
/**
 * ,AI
 * @method function
 * @param  {object} winc 
 * @param  {boolean} isWinnerIsLandlord 
 * @return {number} winnerCardCount 
 */
AILogic.prototype.follow = function (winc, isWinnerIsLandlord, winnerCardCount) {
  var self = this;
  self.log();
  var result = (function () {
    switch (winc.cardKind) {//
      case G.gameRule.ONE://
        var one = self.matchCards(self._one, G.gameRule.ONE, winc, isWinnerIsLandlord, winnerCardCount);
        if (!one) {
          if (isWinnerIsLandlord || self.player.isLandlord) {
            for (var i = 0; i < self.cards.length; i++) {
              if (self.cards[i].val <= 15 && self.cards[i].val > winc.val) {
                return {
                  cardList: self.cards.slice(i, i + 1),
                  cardKind: G.gameRule.ONE,
                  size: 1,
                  val: self.cards[i].val
                };
              }
            }
          }
          if (self.times <= 1 && self._pairs.length > 0 && self._pairs[0].val > 10) {//10
            var c = self.cards.slice(0, 1);
            if (c[0].val > winc.val) {
              return {
                cardList: c,
                cardKind: G.gameRule.ONE,
                size: 1,
                val: c[0].val
              };
            } else {
              return null;
            }
          }
        }
        return one;
      case G.gameRule.PAIRS://
        var pairs = self._pairs.length > 0 ? self.matchCards(self._pairs, G.gameRule.PAIRS, winc, isWinnerIsLandlord, winnerCardCount) : null;
        if (pairs == null && (isWinnerIsLandlord || self.player.isLandlord)) {//
          //
          if (self._progressionPairs.length > 0) {
            for (var i = self._progressionPairs.length - 1; i >= 0; i--) {
              if (winc.val >= self._progressionPairs[i].val) continue;
              for (var j = self._progressionPairs[i].cardList.length - 1; j >= 0; j -= 2) {
                if (self._progressionPairs[i].cardList[j].val > winc.val) {
                  var pairsFromPP = self._progressionPairs[i].cardList.splice(j - 1, 2);
                  return {
                    cardList: pairsFromPP,
                    cardKind: G.gameRule.PAIRS,
                    size: 2,
                    val: pairsFromPP[0].val
                  };
                }
              }
            }
          } else if (self._three.length > 0) {
            for (var i = self._three.length - 1; i >= 0; i--) {
              if (self._three[i].val > winc.val) {
                return {
                  cardList: self._three[i].cardList.slice(0, 2),
                  cardKind: G.gameRule.PAIRS,
                  size: 2,
                  val: self._three[i].val
                };
              }
            }
          }
        }
        return pairs;
      case G.gameRule.THREE://
        if (!isWinnerIsLandlord && !self.player.isLandlord) {
          return null;
        }
        return self.matchCards(self._three, G.gameRule.THREE, winc, isWinnerIsLandlord, winnerCardCount);

      case G.gameRule.THREE_WITH_ONE://
        if (!isWinnerIsLandlord && !self.player.isLandlord) {
          return null;
        }
        var three = self.minCards(self._three, G.gameRule.THREE, winc.val);
        if (three) {
          var one = self.minOne(2, three.val);
          if (!one) {
            return null;
          } else {
            three.cardList.push(one);
          }
          three.cardKind = G.gameRule.THREE_WITH_ONE;
          three.size = 4;
        }
        return three;

      case G.gameRule.THREE_WITH_PAIRS: //
        if (!isWinnerIsLandlord && !self.player.isLandlord) {
          return null;
        }
        var three = self.minCards(self._three, G.gameRule.THREE, winc.val);
        if (three) {
          var pairs = self.minCards(self._pairs, G.gameRule.PAIRS);
          while (true) {//
            if (pairs.cardList[0].val === three.val) {
              pairs = self.minCards(self._pairs, G.gameRule.PAIRS, pairs.cardList[0].val);
            } else {
              break;
            }
          }
          if (pairs) {
            three.cardList = three.cardList.concat(pairs.cardList);
          } else {
            return null;
          }
          three.cardKind = G.gameRule.THREE_WITH_PAIRS;
          three.size = 5;
        }
        return three;

      case G.gameRule.PROGRESSION://
        if (!isWinnerIsLandlord && !self.player.isLandlord) {
          return null;
        }
        if (self._progression.length > 0) {
          for (var i = self._progression.length - 1; i >= 0; i--) {//
            if (winc.val < self._progression[i].val && winc.size <= self._progression[i].cardList.length) {
              if (winc.size === self._progression[i].cardList.length) {
                return self.setCardKind(self._progression[i], G.gameRule.PROGRESSION);
              } else {
                if (self.player.isLandlord || isWinnerIsLandlord) {
                  var valDiff = self._progression[i].val - winc.val,
                    sizeDiff = self._progression[i].cardList.length - winc.size;
                  for (var j = 0; j < sizeDiff; j++) {//
                    if (valDiff > 1) {
                      self._progression[i].cardList.shift();
                      self._progression[i].val--;
                      valDiff--;
                      continue;
                    }
                    self._progression[i].cardList.pop();
                  }
                  return self.setCardKind(self._progression[i], G.gameRule.PROGRESSION);
                } else {
                  return null;
                }
              }
            }
          }
        }
        return null;

      case G.gameRule.PROGRESSION_PAIRS://
        if (!isWinnerIsLandlord && !self.player.isLandlord) {
          return null;
        }
        if (self._progressionPairs.length > 0) {
          for (var i = self._progressionPairs.length - 1; i >= 0; i--) {//
            if (winc.val < self._progressionPairs[i].val && winc.size <= self._progressionPairs[i].cardList.length) {
              if (winc.size === self._progressionPairs[i].cardList.length) {
                return self.setCardKind(self._progressionPairs[i], G.gameRule.PROGRESSION_PAIRS);
              } else {
                if (self.player.isLandlord || isWinnerIsLandlord) {
                  var valDiff = self._progressionPairs[i].val - winc.val,
                    sizeDiff = (self._progressionPairs[i].cardList.length - winc.size) / 2;
                  for (var j = 0; j < sizeDiff; j++) {//
                    if (valDiff > 1) {
                      self._progressionPairs[i].cardList.shift();
                      self._progressionPairs[i].cardList.shift();
                      valDiff--;
                      continue;
                    }
                    self._progressionPairs[i].cardList.pop();
                    self._progressionPairs[i].cardList.pop();
                  }
                  return self.setCardKind(self._progressionPairs[i], G.gameRule.PROGRESSION_PAIRS);
                } else {
                  return null;
                }
              }
            }
          }
        }
        return null;

      case G.gameRule.PLANE://
        if (!isWinnerIsLandlord && !self.player.isLandlord) {
          return null;
        }
        return self.minPlane(winc.size, winc);
      case G.gameRule.PLANE_WITH_ONE: //
        if (!isWinnerIsLandlord && !self.player.isLandlord) {
          return null;
        }
        var cnt = winc.size / 4,
          plane = self.minPlane(cnt * 3, winc);
        if (plane) {
          var currOneVal = 2;
          for (var i = 0; i < cnt; i++) {
            var one = self.minOne(currOneVal, plane.val);//
            if (one) {
              plane.cardList.push(one);
              currOneVal = one.val;
            } else {
              return null;
            }
          }
          plane.cardKind = G.gameRule.PLANE_WITH_ONE;
          plane.size = plane.cardList.length;
        }
        return plane;
      case G.gameRule.PLANE_WITH_PAIRS://
        if (!isWinnerIsLandlord && !self.player.isLandlord) {
          return null;
        }
        var cnt = winc.size / 5,
          plane = self.minPlane(cnt * 3, winc);
        if (plane) {
          var currPairsVal = 2;
          for (var i = 0; i < cnt; i++) {
            var pairs = self.minCards(self._pairs, G.gameRule.PAIRS, currPairsVal);//
            if (pairs) {
              plane.cardList = plane.cardList.concat(pairs.cardList);
              currPairsVal = pairs.val;
            } else {
              return null;
            }
          }
          plane.cardKind = G.gameRule.PLANE_WITH_PAIRS;
          plane.size = plane.cardList.length;
        }
        return plane;

      case G.gameRule.BOMB://
        if (!isWinnerIsLandlord && !self.player.isLandlord) {//
          return null;
        }
        var bomb = self.minCards(self._bomb, G.gameRule.BOMB, winc.val);
        if (bomb) {
          return bomb;
        } else {
          if (self._kingBomb.length > 0) {
            if ((isWinnerIsLandlord && winnerCardCount < 6)
              || (self.player.isLandlord && self.player.cardList.length < 6)) {
              return self.setCardKind(self._kingBomb[0], G.gameRule.KING_BOMB);
            }
          }
          return null;
        }
      case G.gameRule.FOUR_WITH_TWO:
        return self.minCards(self._bomb, G.gameRule.BOMB, winc.val);
      case G.gameRule.FOUR_WITH_TWO_PAIRS:
        return self.minCards(self._bomb, G.gameRule.BOMB, winc.val);
      case G.gameRule.KING_BOMB:
        return null;
      default:
        return null;
    }
  })();

  //
  if (result) {
    return result;
  } else if (winc.cardKind != G.gameRule.BOMB && winc.cardKind != G.gameRule.KING_BOMB
    && (self._bomb.length > 0 || self._kingBomb.length > 0)) {
    if ((isWinnerIsLandlord && winnerCardCount < 5)
      || (self.player.isLandlord && (self.player.cardList.length < 5 || (self.player.nextPlayer.cardList.length < 5 || self.player.nextPlayer.nextPlayer.cardList.length < 6)))
      || self.times() <= 2) {//
      if (self._bomb.length > 0) {
        return self.minCards(self._bomb, G.gameRule.BOMB);
      } else {
        return self.setCardKind(self._kingBomb[0], G.gameRule.KING_BOMB);
      }
    }
  } else {
    return null;
  }
};

/**
 * ,
 * @method function
 * @return {array} [description]
 */
AILogic.prototype.play = function (landlordCardsCnt) {
  var self = this;
  self.log();
  var cardsWithMin = function (idx) {
    var minCard = self.cards[idx];
    //
    for (var i = 0; i < self._one.length; i++) {
      if (self._one[i].val === minCard.val) {
        return self.minCards(self._one, G.gameRule.ONE);
      }
    }
    //
    for (let i = 0; i < self._pairs.length; i++) {
      if (self._pairs[i].val === minCard.val) {
        return self.minCards(self._pairs, G.gameRule.PAIRS);
      }
    }
    //
    for (let i = 0; i < self._three.length; i++) {
      if (self._three[i].val === minCard.val) {
        return self.minCards(self._three, G.gameRule.THREE);
      }
    }
    //
    for (let i = 0; i < self._bomb.length; i++) {
      if (self._bomb[i].val === minCard.val) {
        return self.minCards(self._bomb, G.gameRule.BOMB);
      }
    }
    //
    for (let i = 0; i < self._plane.length; i++) {
      for (var j = 0; j < self._plane[i].cardList.length; j++) {
        if (self._plane[i].cardList[j].val === minCard.val && self._plane[i].cardList[j].shape === minCard.shape) {
          return self.minCards(self._plane, G.gameRule.PLANE);
        }
      }
    }
    //
    for (let i = 0; i < self._progression.length; i++) {
      for (var j = 0; j < self._progression[i].cardList.length; j++) {
        if (self._progression[i].cardList[j].val === minCard.val && self._progression[i].cardList[j].shape === minCard.shape) {
          return self.minCards(self._progression, G.gameRule.PROGRESSION);
        }
      }
    }
    //
    for (let i = 0; i < self._progressionPairs.length; i++) {
      for (var j = 0; j < self._progressionPairs[i].cardList.length; j++) {
        if (self._progressionPairs[i].cardList[j].val === minCard.val && self._progressionPairs[i].cardList[j].shape === minCard.shape) {
          return self.minCards(self._progressionPairs, G.gameRule.PROGRESSION_PAIRS);
        }
      }
    }
    if (self._kingBomb.length > 0) {
      return self.minCards(self._kingBomb, G.gameRule.KING_BOMB);
    }
  };
  for (var i = self.cards.length - 1; i >= 0; i--) {
    var r = cardsWithMin(i);
    if (r.cardKind === G.gameRule.ONE) {
      if (self._plane.length > 0) {//
        var plane = self.minCards(self._plane, G.gameRule.PLANE);
        var len = plane.cardList.length / 3;
        var currOneVal = 2;
        for (var i = 0; i < len; i++) {
          var one = self.minOne(currOneVal, plane.val);//
          plane.cardList.push(one);
          currOneVal = one.val;
        }
        return self.setCardKind(plane, G.gameRule.PLANE_WITH_ONE);
      }
      else if (self._three.length > 0) {//
        var three = self.minCards(self._three, G.gameRule.THREE);
        var len = three.cardList.length / 3;
        var one = self.minOne(currOneVal, three.val);//
        three.cardList.push(one);
        if (three.val < 14)
          return self.setCardKind(three, G.gameRule.THREE_WITH_ONE);
      }
      if (self.player.isLandlord) {//
        if (self.player.isLandlord) {//
          if (self.player.nextPlayer.cardList.length <= 2 || self.player.nextPlayer.nextPlayer.cardList.length <= 2)
            return self.playOneAtTheEnd(landlordCardsCnt);
          else
            return self.minCards(self._one, G.gameRule.ONE);
        }
      } else {//
        if (landlordCardsCnt <= 2)
          return self.playOneAtTheEnd(landlordCardsCnt);
        else
          return self.minCards(self._one, G.gameRule.ONE);
      }
    } else if (r.cardKind === G.gameRule.THREE) {
      var three = self.minCards(self._three, G.gameRule.THREE);
      var len = three.cardList.length / 3;
      if (self._one.length > 0) {//
        var one = self.minOne(currOneVal, three.val);//
        three.cardList.push(one);
        return self.setCardKind(three, G.gameRule.THREE_WITH_ONE);
      } else if (self._pairs.length > 0) {
        var pairs = self.minCards(self._pairs, G.gameRule.PAIRS, currPairsVal);//
        three.cardList = three.cardList.concat(pairs.cardList);
        return self.setCardKind(three, G.gameRule.THREE_WITH_PAIRS);
      } else {
        return self.setCardKind(three, G.gameRule.THREE);
      }
    } else if (r.cardKind === G.gameRule.PLANE) {
      var plane = self.minCards(self._plane, G.gameRule.PLANE);
      var len = plane.cardList.length / 3;
      if (self._one.length > len && self._pairs.length > len) {
        if (self._one.length >= self._pairs.length) {//
          var currOneVal = 2;
          for (var i = 0; i < len; i++) {
            var one = self.minOne(currOneVal, plane.val);//
            plane.cardList.push(one);
            currOneVal = one.val;
          }
          return self.setCardKind(plane, G.gameRule.PLANE_WITH_ONE);
        } else {
          var currPairsVal = 2;
          for (var i = 0; i < len; i++) {
            var pairs = self.minCards(self._pairs, G.gameRule.PAIRS, currPairsVal);//
            plane.cardList = plane.cardList.concat(pairs.cardList);
            currPairsVal = pairs.val;
          }
          return self.setCardKind(plane, G.gameRule.PLANE_WITH_PAIRS);
        }
      } else if (self._pairs.length > len) {
        var currPairsVal = 2;
        for (var i = 0; i < len; i++) {
          var pairs = self.minCards(self._pairs, G.gameRule.PAIRS, currPairsVal);//
          plane.cardList = plane.cardList.concat(pairs.cardList);
          currPairsVal = pairs.val;
        }
        return self.setCardKind(plane, G.gameRule.PLANE_WITH_PAIRS);
      } else if (self._one.length > len) {
        var currOneVal = 2;
        for (var i = 0; i < len; i++) {
          var one = self.minOne(currOneVal, plane.val);//
          plane.cardList.push(one);
          currOneVal = one.val;
        }
        return self.setCardKind(plane, G.gameRule.PLANE_WITH_ONE);
      } else {
        return self.setCardKind(plane, G.gameRule.PLANE);
      }
    } else if (r.cardKind === G.gameRule.BOMB && self.times() === 1) {
      return r;
    } else if (r.cardKind === G.gameRule.BOMB && self.times() != 1) {
      continue;
    } else {
      return r;
    }
  }
};
//
AILogic.prototype.playOneAtTheEnd = function (landlordCardsCnt) {
  var self = this;
  if (self._progression.length > 0) {//
    return self.minCards(self._progression, G.gameRule.PROGRESSION);
  }
  else if (self._plane.length > 0) {//
    var plane = self.minCards(self._plane, G.gameRule.PLANE);
    var len = plane.cardList.length / 3;
    if (self._one.length > len && self._pairs.length > len) {
      if (self._one.length >= self._pairs.length) {//
        var currOneVal = 2;
        for (var i = 0; i < len; i++) {
          var one = self.minOne(currOneVal, plane.val);//
          plane.cardList.push(one);
          currOneVal = one.val;
        }
        return self.setCardKind(plane, G.gameRule.PLANE_WITH_ONE);
      } else {
        var currPairsVal = 2;
        for (var i = 0; i < len; i++) {
          var pairs = self.minCards(self._pairs, G.gameRule.PAIRS, currPairsVal);//
          plane.cardList = plane.cardList.concat(pairs.cardList);
          currPairsVal = pairs.val;
        }
        return self.setCardKind(plane, G.gameRule.PLANE_WITH_PAIRS);
      }
    } else if (self._pairs.length > len) {
      var currPairsVal = 2;
      for (var i = 0; i < len; i++) {
        var pairs = self.minCards(self._pairs, G.gameRule.PAIRS, currPairsVal);//
        plane.cardList = plane.cardList.concat(pairs.cardList);
        currPairsVal = pairs.val;
      }
      return self.setCardKind(plane, G.gameRule.PLANE_WITH_PAIRS);
    } else if (self._one.length > len) {
      var currOneVal = 2;
      for (var i = 0; i < len; i++) {
        var one = self.minOne(currOneVal, plane.val);//
        plane.cardList.push(one);
        currOneVal = one.val;
      }
      return self.setCardKind(plane, G.gameRule.PLANE_WITH_ONE);
    } else {
      return self.setCardKind(plane, G.gameRule.PLANE);
    }
  }
  else if (self._progressionPairs.length > 0) {//
    return self.minCards(self._progressionPairs, G.gameRule.PROGRESSION_PAIRS);
  }
  else if (self._three.length > 0) {//
    var three = self.minCards(self._three, G.gameRule.THREE);
    var len = three.cardList.length / 3;
    if (self._one.length >= 0) {//
      var one = self.minOne(currOneVal, three.val);//
      three.cardList.push(one);
      return self.setCardKind(three, G.gameRule.THREE_WITH_ONE);
    } else if (self._pairs.length > 0) {
      var pairs = self.minCards(self._pairs, G.gameRule.PAIRS, currPairsVal);//
      three.cardList = three.cardList.concat(pairs.cardList);
      return self.setCardKind(three, G.gameRule.THREE_WITH_PAIRS);
    } else {
      return self.setCardKind(three, G.gameRule.THREE);
    }
  }
  else if (self._pairs.length > 0) {//
    if ((self.player.isLandlord && (self.player.nextPlayer.cardList.length === 2 || self.player.nextPlayer.nextPlayer.cardList.length === 2))
      || (!self.player.isLandlord && landlordCardsCnt === 2))
      return self.maxCards(self._pairs, G.gameRule.PAIRS);
    else
      return self.minCards(self._pairs, G.gameRule.PAIRS);
  }
  else if (self._one.length > 0) {//
    if ((self.player.isLandlord && (self.player.nextPlayer.cardList.length <= 2 || self.player.nextPlayer.nextPlayer.cardList.length <= 2))
      || (!self.player.isLandlord && landlordCardsCnt <= 2))
      return self.maxCards(self._one, G.gameRule.ONE);
    else
      return self.minCards(self._one, G.gameRule.ONE);
  } else {//
    var one = null;
    if ((self.player.isLandlord && (self.player.nextPlayer.cardList.length <= 2 || self.player.nextPlayer.nextPlayer.cardList.length <= 2))
      || (!self.player.isLandlord && landlordCardsCnt <= 2))
      one = self.cards.slice(self.cards.length - 1, self.cards.length);
    else
      one = self.cards.slice(0, 1);
    return {
      size: 1,
      cardKind: G.gameRule.ONE,
      cardList: one,
      val: one[0].val
    };
  }
};

/**
 * 
 * @method prompt
 * @param  {object} winc 
 * @return {Array}      
 */
AILogic.prototype.prompt = function (winc) {
  var self = this,
    stat = G.gameRule.valCount(self.cards);

  if (winc) {//
    var promptList = [];
    /**
     * 
     * @method function
     * @param  {int} c 12
     * @param  {int} winVal 
     * @param  {array} st 
     */
    var setPrompt = function (c, winVal, st) {
      var result = [];
      //
      for (var i = st.length - 1; i >= 0; i--) {
        if (st[i].count < c || st[i].val <= winVal) {
          st.splice(i, 1);
        }
      }
      st.sort(self.promptSort);
      //
      for (let i = 0; i < st.length; i++) {
        for (let j = 0; j < self.cards.length; j++) {
          if (self.cards[j].val === st[i].val) {
            result.push(self.cards.slice(j, j + c));
            break;
          }
        }
      }
      return result;
    };
    /**
     * 
     * @method function
     * @param  {int} n ()
     */
    var getPlanePrompt = function (n) {
      var result = [];
      if (winc.val < 14 && self.cards.length >= winc.size) {//
        for (var i = winc.val + 1; i < 15; i++) {
          var proList = [];
          for (var j = 0; j < self.cards.length; j++) {
            if (self.cards[j].val < i && proList.length === 0) break;
            if (self.cards[j].val > i || (proList.length > 0 && self.cards[j].val === proList[proList.length - 1].val)) {
              continue;
            }
            if (self.cards[j].val === i
              && self.cards[j + 1]
              && self.cards[j + 1].val === i
              && self.cards[j + 2]
              && self.cards[j + 2].val === i
              && proList.length === 0) {
              proList = proList.concat(self.cards.slice(j, j + 3));
              j += 2;
              continue;
            }
            if (proList.length > 0
              && proList[proList.length - 1].val - 1 === self.cards[j].val
              && self.cards[j + 1]
              && proList[proList.length - 1].val - 1 === self.cards[j + 1].val
              && self.cards[j + 2]
              && proList[proList.length - 1].val - 1 === self.cards[j + 2].val) {//
              proList = proList.concat(self.cards.slice(j, j + 3));
              j += 2;
              if (proList.length === n * 3) {
                result.push(proList);
                break;
              }
            } else { break; }
          }
        }
      }
      return result;
    };
    switch (winc.cardKind) {
      case G.gameRule.ONE://
        promptList = setPrompt(1, winc.val, stat);
        break;
      case G.gameRule.PAIRS://
        promptList = setPrompt(2, winc.val, stat);
        break;
      case G.gameRule.THREE://
        promptList = setPrompt(3, winc.val, stat);
        break;
      case G.gameRule.THREE_WITH_ONE://
        var threePrompt = setPrompt(3, winc.val, stat.slice(0)),
          onePrompt = setPrompt(1, 2, stat.slice(0));
        for (var i = 0; i < threePrompt.length; i++) {
          for (var j = 0; j < onePrompt.length; j++) {
            if (onePrompt[j][0].val != threePrompt[i][0].val) {
              promptList.push(threePrompt[i].concat(onePrompt[j]));
            }
          }
        }
        break;
      case G.gameRule.THREE_WITH_PAIRS://
        var threePrompt = setPrompt(3, winc.val, stat.slice(0)),
          pairsPrompt = setPrompt(2, 2, stat.slice(0));
        for (var i = 0; i < threePrompt.length; i++) {
          for (var j = 0; j < pairsPrompt.length; j++) {
            if (pairsPrompt[j][0].val != threePrompt[i][0].val) {
              promptList.push(threePrompt[i].concat(pairsPrompt[j]));
            }
          }
        }
        break;
      case G.gameRule.PROGRESSION://
        if (winc.val < 14 && self.cards.length >= winc.size) {//
          for (var i = winc.val + 1; i < 15; i++) {
            var proList = [];
            for (var j = 0; j < self.cards.length; j++) {
              if (self.cards[j].val < i && proList.length === 0) break;
              if (self.cards[j].val > i || (proList.length > 0 && self.cards[j].val === proList[proList.length - 1].val)) {
                continue;
              }
              if (self.cards[j].val === i && proList.length === 0) {
                proList.push(self.cards.slice(j, j + 1)[0]);
                continue;
              }
              if (proList[proList.length - 1].val - 1 === self.cards[j].val) {//
                proList.push(self.cards.slice(j, j + 1)[0]);
                if (proList.length === winc.size) {
                  promptList.push(proList);
                  break;
                }
              } else { break; }
            }
          }
        }
        break;
      case G.gameRule.PROGRESSION_PAIRS://
        if (winc.val < 14 && self.cards.length >= winc.size) {//
          for (var i = winc.val + 1; i < 15; i++) {
            var proList = [];
            for (var j = 0; j < self.cards.length; j++) {
              if (self.cards[j].val < i && proList.length === 0) break;
              if (self.cards[j].val > i || (proList.length > 0 && self.cards[j].val === proList[proList.length - 1].val)) {
                continue;
              }
              if (self.cards[j].val === i && self.cards[j + 1] && self.cards[j + 1].val === i && proList.length === 0) {
                proList = proList.concat(self.cards.slice(j, j + 2));
                j++;
                continue;
              }
              if (proList.length > 0
                && proList[proList.length - 1].val - 1 === self.cards[j].val
                && self.cards[j + 1]
                && proList[proList.length - 1].val - 1 === self.cards[j + 1].val) {//
                proList = proList.concat(self.cards.slice(j, j + 2));
                j++;
                if (proList.length === winc.size) {
                  promptList.push(proList);
                  break;
                }
              } else { break; }
            }
          }
        }
        break;
      case G.gameRule.PLANE://
        promptList = getPlanePrompt(winc.size / 3);
        break;
      case G.gameRule.PLANE_WITH_ONE:
        promptList = getPlanePrompt(winc.size / 4);
        break;
      case G.gameRule.PLANE_WITH_PAIRS:
        promptList = getPlanePrompt(winc.size / 5);
        break;
      case G.gameRule.FOUR_WITH_TWO:
        promptList = setPrompt(4, winc.val, stat);
        break;
      case G.gameRule.FOUR_WITH_TWO_PAIRS:
        promptList = setPrompt(4, winc.val, stat);
        break;
      case G.gameRule.BOMB:
        promptList = setPrompt(4, winc.val, stat);
        break;
      default:
        break;
    }
    if (winc.cardKind != G.gameRule.KING_BOMB && winc.cardKind != G.gameRule.BOMB) {
      //
      if (self._bomb.length > 0) {
        for (var i = self._bomb.length - 1; i >= 0; i--) {
          promptList.push(self._bomb[i].cardList);
        }
      }
    }
    if (winc.cardKind != G.gameRule.KING_BOMB) {
      //
      if (self._kingBomb.length > 0) {
        promptList.push(self._kingBomb[0].cardList);
      }
    }
    return promptList;
  } else {//
    var promptList = [];
    for (var i = stat.length - 1; i >= 0; i--) {
      if (i != 0) {
        promptList.push(self.cards.splice(self.cards.length - stat[i].count, self.cards.length - 1));
      } else {
        promptList.push(self.cards);
      }
    }
    return promptList;
  }
};

/**
 * 
 * @param  {list} cards - 
 * @param  {number} n - 
 * @param  {number} n - 
 * @return 
 */
AILogic.prototype.getMinVal = function (n, v) {
  var self = this,
    c = G.gameRule.valCount(self.cards);
  for (var i = c.length - 1; i >= 0; i--) {
    if (c[i].count === n && c[i].val > v) {
      return self.cards.splice(i, 1);
    }
  }
};

/**
 * 
 * @method function
 * @return {[shape]} [description]
 */
AILogic.prototype.analyse = function () {
  var self = this,
    target = self.cards.slice(0),//
    stat = null,//
    targetWob = null,//
    targetWobt = null,//
    targetWobp = null,//
    targetWobpp = null;//
  //
  self._one = [];
  self._pairs = [];
  self._kingBomb = [];
  self._bomb = [];
  self._three = [];
  self._plane = [];
  self._progression = [];
  self._progressionPairs = [];
  target.sort(G.gameRule.cardSort);
  //
  if (G.gameRule.isKingBomb(target.slice(0, 2))) {
    self._kingBomb.push(AICardType(17, target.splice(0, 2)));
  }
  //
  stat = G.gameRule.valCount(target);
  for (var i = 0; i < stat.length; i++) {
    if (stat[i].count === 4) {
      var list = [];
      self.moveItem(target, list, stat[i].val);
      self._bomb.push(AICardType(list[0].val, list));
    }
  }
  targetWob = target.slice(0);
  //
  targetWobt = targetWob.slice(0);
  self.judgeThree(targetWobt);
  //()
  self.judgePlane();

  //
  for (let i = 0; i < self._three.length; i++) {
    targetWobt = targetWobt.concat(self._three[i].cardList);
  }
  self._three = [];
  targetWobt.sort(G.gameRule.cardSort);
  //
  targetWobp = targetWobt.slice(0);
  self.judgeProgression(targetWobp);
  //
  //targetWobpp = targetWobp.slice(0);
  self.judgeProgressionPairs(targetWobp);
  //
  //targetWobt = targetWob.slice(0);
  self.judgeThree(targetWobp);
  //
  stat = G.gameRule.valCount(targetWobp);
  for (let i = 0; i < stat.length; i++) {
    if (stat[i].count === 1) {//
      for (var j = 0; j < targetWobp.length; j++) {
        if (targetWobp[j].val === stat[i].val) {
          self._one.push(AICardType(stat[i].val, targetWobp.splice(j, 1)));
        }
      }
    } else if (stat[i].count === 2) {//
      for (var j = 0; j < targetWobp.length; j++) {
        if (targetWobp[j].val === stat[i].val) {
          self._pairs.push(AICardType(stat[i].val, targetWobp.splice(j, 2)));
        }
      }
    }
  }

};

/**
 * 
 * @method judgeThree
 */
AILogic.prototype.judgeThree = function (cards) {
  var self = this,
    stat = G.gameRule.valCount(cards);
  for (let i = 0; i < stat.length; i++) {
    if (stat[i].count === 3) {
      var list = [];
      self.moveItem(cards, list, stat[i].val);
      self._three.push(AICardType(list[0].val, list));
    }
  }
};

/**
 * 
 * @method judgePlane
 */
AILogic.prototype.judgePlane = function () {
  var self = this;
  if (self._three.length > 1) {
    var proList = [];
    for (let i = 0; i < self._three.length; i++) {//
      if (self._three[i].val >= 15) continue;//2
      if (proList.length == 0) {
        proList.push({ 'obj': self._three[i], 'fromIndex': i });
        continue;
      }
      if (proList[proList.length - 1].val - 1 == self._three[i].val) {//
        proList.push({ 'obj': self._three[i], 'fromIndex': i });
      } else {
        if (proList.length > 1) {//
          var planeCards = [];
          for (var j = 0; j < proList.length; j++) {
            planeCards = planeCards.concat(proList[j].obj.cardList);
          }
          self._plane.push(AICardType(proList[0].obj.val, planeCards));
          for (var k = proList.length - 1; k >= 0; k--) {//
            self._three.splice(proList[k].fromIndex, 1);
          }
        }
        //
        proList = [];
        proList.push({ 'obj': self._three[i], 'fromIndex': i });
      }
    }
    if (proList.length > 1) {//
      var planeCards = [];
      for (var j = 0; j < proList.length; j++) {
        planeCards = planeCards.concat(proList[j].obj.cardList);
      }
      self._plane.push(AICardType(proList[0].obj.val, planeCards));
      for (var k = proList.length - 1; k >= 0; k--) {//
        self._three.splice(proList[k].fromIndex, 1);
      }
    }
  }
};

/**
 * ()
 * @method judgeProgression
 * @param  {[array]}         cards 
 */
AILogic.prototype.judgeProgression = function (cards) {
  var self = this;

  var saveProgression = function (proList) {
    var progression = [];
    for (var j = 0; j < proList.length; j++) {
      progression.push(proList[j].obj);
    }
    self._progression.push(AICardType(proList[0].obj.val, progression));
    for (var k = proList.length - 1; k >= 0; k--) {//
      cards.splice(proList[k].fromIndex, 1);
    }
  };
  //
  if (cards.length >= 5) {
    var proList = [];
    for (var i = 0; i < cards.length; i++) {
      if (cards[i].val >= 15) continue;//2
      if (proList.length == 0) {
        proList.push({ 'obj': cards[i], 'fromIndex': i });
        continue;
      }
      if (proList[proList.length - 1].obj.val - 1 === cards[i].val) {//
        proList.push({ 'obj': cards[i], 'fromIndex': i });
        if (proList.length === 5) break;
      } else if (proList[proList.length - 1].obj.val === cards[i].val) {//
        continue;
      } else {
        if (proList.length >= 5) {//
          //saveProgression(proList);
          //proList = [];
          break;
        } else {
          //
          proList = [];
          proList.push({ 'obj': cards[i], 'fromIndex': i });
        }
      }
    }
    if (proList.length === 5) {//
      saveProgression(proList);
      self.judgeProgression(cards);//
    } else {
      self.joinProgression(cards);
    }
  }
};

/**
 * 
 * @method judgeProgression
 * @param  {[array]}         cards 
 */
AILogic.prototype.joinProgression = function (cards) {
  var self = this;
  for (var i = 0; i < self._progression.length; i++) {//
    for (var j = 0; j < cards.length; j++) {
      if (self._progression[i].val != 14 && self._progression[i].val === cards[j].val - 1) {
        self._progression[i].cardList.unshift(cards.splice(j, 1)[0]);
      } else if (cards[j].val === self._progression[i].val - self._progression[i].cardList.length) {
        self._progression[i].cardList.push(cards.splice(j, 1)[0]);
      }
    }
  }
  var temp = self._progression.slice(0);
  for (let i = 0; i < temp.length; i++) {//
    if (i < temp.length - 1 && temp[i].val - temp[i].cardList.length === temp[i + 1].val) {
      self._progression[i].cardList = self._progression[i].cardList.concat(self._progression[i + 1].cardList);
      self._progression.splice(++i, 1);
    }
  }
};

/**
 * 
 * @method judgeProgressionPairs
 * @param  {[array]}         cards 
 */
AILogic.prototype.judgeProgressionPairs = function (cards) {
  var self = this;

  var saveProgressionPairs = function (proList) {
    var progressionPairs = [];
    for (var i = proList.length - 1; i >= 0; i--) {//
      for (var j = 0; j < cards.length; j++) {
        if (cards[j].val === proList[i]) {
          progressionPairs = progressionPairs.concat(cards.splice(j, 2));
          break;
        }
      }
    }
    progressionPairs.sort(G.gameRule.cardSort);
    self._progressionPairs.push(AICardType(proList[0], progressionPairs));
  };
  //
  if (cards.length >= 6) {
    var proList = [];
    var stat = G.gameRule.valCount(cards);//
    for (var i = 0; i < stat.length; i++) {
      if (stat[i].val >= 15) {//2
        continue;
      }
      if (proList.length == 0 && stat[i].count >= 2) {
        proList.push(stat[i].val);
        continue;
      }
      if (proList[proList.length - 1] - 1 === stat[i].val && stat[i].count >= 2) {//
        proList.push(stat[i].val);
      } else {
        if (proList.length >= 3) {//
          //saveProgressionPairs(proList);
          //proList = [];
          break;
        } else {
          //
          proList = [];
          if (stat[i].count >= 2) proList.push(stat[i].val);
        }
      }
    }
    if (proList.length >= 3) {//
      saveProgressionPairs(proList);
      self.judgeProgressionPairs(cards);
    }
  }
};

/**
 * srcdest
 */
AILogic.prototype.moveItem = function (src, dest, v) {
  for (var i = src.length - 1; i >= 0; i--) {
    if (src[i].val === v) {
      dest.push(src.splice(i, 1)[0]);
    }
  }
};

/**
 * 
 * @method setCardKind
 * @param  {[object]}    obj  
 * @param  {[kind]}    kind 
 */
AILogic.prototype.setCardKind = function (obj, kind) {
  obj.cardKind = kind;
  obj.size = obj.cardList.length;
  return obj;
};

/**
 * 
 * 
 * @return
 */
AILogic.prototype.minPlane = function (len, winc) {
  var self = this;
  if (self._plane.length > 0) {
    for (var i = self._plane.length - 1; i >= 0; i--) {//
      if (winc.val < self._plane[i].val && len <= self._plane[i].cardList.length) {
        if (len === self._plane[i].cardList.length) {
          return self.setCardKind(self._plane[i], G.gameRule.PLANE);
        } else {
          var valDiff = self._plane[i].val - winc.val,
            sizeDiff = (self._plane[i].cardList.length - len) / 3;
          for (var j = 0; j < sizeDiff; j++) {//
            if (valDiff > 1) {
              for (var k = 0; k < 3; k++) {
                self._plane[i].cardList.shift();
              }
              valDiff--;
              continue;
            }
            for (var k = 0; k < 3; k++) {
              self._plane[i].cardList.pop();
            }
          }
          return self.setCardKind(self._plane[i], G.gameRule.PLANE);
        }
      }
    }
  }
  return null;
};

/**
 * listv
 * @param  {array} list [description]
 * @param  {number} kind    
 * @param  {number} v    
 * @return
 */
AILogic.prototype.minCards = function (list, kind, v) {
  var self = this;
  v = v ? v : 2;
  if (list.length > 0) {
    for (var i = list.length - 1; i >= 0; i--) {//
      if (v < list[i].val) {
        return self.setCardKind(list[i], kind);
      }
    }
  }
  return null;
};

/**
 * list
 * @param  {array} list [description]
 * @param  {number} kind    
 * @param  {number} v    
 * @return
 */
AILogic.prototype.maxCards = function (list, kind, v) {
  var self = this,
    max = null;
  if (list.length > 0) {
    for (var i = 0; i < list.length; i++) {//
      if ((max && list[i].val > max.val) || !max) {
        max = list[i];
      }
    }
    return v ? (max.val > v ? self.setCardKind(max, kind) : null) : self.setCardKind(max, kind);
  }
  return null;
};

/**
 * 
 * @method function
 * @param  {[array]} list 
 * @return {[number]}      
 * @param  {object} winc 
 * @param  {boolean} isWinnerIsLandlord 
 * @return {number} winnerCardCount 
 */
AILogic.prototype.matchCards = function (list, kind, winc, isWinnerIsLandlord, winnerCardCount) {
  var self = this;
  if (self.player.isLandlord) {//
    if (self.player.nextPlayer.cardList.length < 3 || self.player.nextPlayer.nextPlayer.cardList.length < 3)
      return self.maxCards(list, kind, winc.val);
    else
      return self.minCards(list, kind, winc.val);
  } else {//
    if (isWinnerIsLandlord) {//
      if (winnerCardCount < 3) {
        return self.maxCards(list, kind, winc.val);
      } else {
        return self.minCards(list, kind, winc.val);
      }
    } else {
      var c = null;
      if (self.player.nextPlayer.isLandlord && self.player.nextPlayer.cardList.length < 3) {
        return self.maxCards(list, kind, winc.val);
      } else {
        c = self.minCards(list, kind, winc.val);
        return c ? (c.val < 14 || self.times() <= 2 ? c : null) : null;
      }
    }
  }
};
/**
 * 
 * @param  {array} list [description]
 * @param  {number} v    
 * * @param  {number} notEq    
 * @return
 */
AILogic.prototype.minOne = function (v, notEq) {
  var self = this,
    one = self.minCards(self._one, G.gameRule.ONE, v),
    oneFromPairs = self.offPairs(notEq);
  if (!one) {//
    if (oneFromPairs) {
      self.deleteOne(oneFromPairs);
      return oneFromPairs;
    } else {
      return null;
    }
  } else {
    if (one.val > 14) {//2
      if (oneFromPairs) {
        self.deleteOne(oneFromPairs);
        return oneFromPairs;
      } else
        return null;
    } else {
      return one.cardList[0];
    }
  }
  return null;
};

/**
 * 
 * @param  {number} v 
 * @param  {number} notEq 
 * @return {card}    
 */
AILogic.prototype.offPairs = function (v, notEq) {
  var self = this,
    pairs = self.minCards(self._pairs, G.gameRule.PAIRS, v);
  if (pairs) {
    while (true) {
      if (pairs.cardList[0].val === notEq) {
        pairs = self.minCards(self._pairs, G.gameRule.PAIRS, pairs.cardList[0].val);
      } else {
        break;
      }
    }
  }

  return pairs ? pairs.cardList[0] : null;
};
/*
    
 */
AILogic.prototype.deleteOne = function (card) {
  for (var i = 0; i < this.cards.length; i++) {
    if (this.cards[i].val === card.val && this.cards[i].shape === card.shape) {
      this.cards.splice(i, 1);
    }
  }
  this.analyse();
};
/**
 * ,AI
 * @method function
 * @return {[nmber]} 
 */
AILogic.prototype.judgeScore = function () {
  var self = this,
    score = 0;
  score += self._bomb.length * 6;//
  if (self._kingBomb.length > 0) {//8
    score += 8;
  } else {
    if (self.cards[0].val === 17) {
      score += 4;
    } else if (self.cards[0].val === 16) {
      score += 3;
    }
  }
  for (var i = 0; i < self.cards.length; i++) {
    if (self.cards[i].val === 15) {
      score += 2;
    }
  }
  console.info(self.player.userId + "" + score);
  if (score >= 7) {
    return 3;
  } else if (score >= 5) {
    return 2;
  } else if (score >= 3) {
    return 1;
  } else {//4
    return 4;
  }
};

//
//
AILogic.prototype.promptSort = function (a, b) {
  if (a.count === b.count) {
    return a.val > b.val ? 1 : -1;
  } if (a.count < b.count) {
    return -1;
  } else {
    return 1;
  }
}

/**
 * 
 * @method times
 */
AILogic.prototype.times = function () {
  var t = this._kingBomb.length +
    this._bomb.length +
    this._progression.length +
    this._progressionPairs.length +
    this._one.length +
    this._pairs.length;
  var threeCount = this._three.length;
  if (this._plane.length > 0) {
    for (var i = 0; i < this._plane.length; i++) {
      threeCount += this._plane[i].cardList.length / 3;
    }
  }
  if (threeCount - (this._one.length + this._pairs.length) > 0) {
    t += threeCount - (this._one.length + this._pairs.length);
  }
  return t;
};

AILogic.prototype.log = function () {
  var self = this;
  console.info('' + self.player.userId + '' + self.times());
  console.info('');
  console.info(self._kingBomb);
  console.info('');
  console.info(self._bomb);
  console.info('');
  console.info(self._three);
  console.info('');
  console.info(self._plane);
  console.info('');
  console.info(self._progression);
  console.info('');
  console.info(self._progressionPairs);
  console.info('');
  console.info(self._one);
  console.info('');
  console.info(self._pairs);
};

module.exports = AILogic
