const audioManager = require('audioManager');

const common = {
  // 
  audio: audioManager,
  // 
  getRandomStr(count) {
    var str = '';
    for (var i = 0; i < count; i++) {
      str += Math.floor(Math.random() * 10);
    }
    return str;
  },
  /**
    * 
    * @param {Number} lower 
    * @param {Number} upper 
    * @return {Number} 
    */
  random(lower, upper) {
    return Math.round(Math.random() * (upper-lower) + lower)
  }
}

module.exports = common
